import uniRate from '../../components/uni-rate.vue'
export default {
	data() {
		return {
			id: '',
			dayId: '',
			// level:'',
			isBody:0,
			pageNumber:1,
			pageSize:20,
			footData: []
		}
	},
	components:{
		uniRate
	},
	onLoad(e) {
		// console.log(e)
		this.id = e.id
		this.dayId = e.dayId
		this.req('')
	},
	methods: {
		req(level){
			let that = this
			uni.request({
				url:this.nowUrl + '/foods/comment',
				header:{
					'token':this.token
				},
				data:{
					id: this.id,
					level: level,
					isBody:this.isBody,
					pageNumber:this.pageNumber,
					pageSize:this.pageSize
				},
				success(res) {
					// console.log(res)
					that.footData = res.data.data.list
					that.flag = true
					console.log(that.footData)
					uni.hideLoading()
				}
			})
		}
	}
}
